<template>
    <div class="result-wrapper">
        <div class="card light result mt-20">
            <div class="card-header">
                <div class="card-title summary">거래내역 <span>{{countsOfTras}}</span>건</div>
                <JsonExcel v-if="logStatus.downEx && smode=='pay'" class="btn outline btn-excel authorized" :data="resultData" :fields="orderFields" :name="dnExcelName" :beforeGenerate="setDownloadName" :stringifyLongNum="sln"><i class="fas fa-download"></i>&nbsp;</JsonExcel>
                <button v-else-if="logStatus.downEx && smode=='order'" class="btn outline btn-excel authorized" @click="downloadExcel"><i class="fas fa-download"></i>&nbsp;</button>
                <button v-else type="button" class="btn outline btn-excel" @click="auth4ExcelDownload"><i class="fas fa-download"></i>&nbsp;</button>
            </div>
            <div class="card-body">
                <table v-if="countsOfTras>0">
                    <tr>
                        <th>결제건명</th>
                        <th>이용자명</th>
                        <th>결제수단</th>
                        <th>결제금액</th>
                        <th>할인율</th>
                        <th>출판사</th>
                        <th>주문번호</th>
                        <th>결제일시</th>
                        <th></th>
                    </tr>
                    <tr v-for='item in resultData' :key='item.tid' class="hoverable">
                            <td>
                                <span v-if="item.status=='cancel'" class="badge badge-pill badge-warning mr-10" style="text-transform: capitalize;">{{ item.status.charAt(0) }}</span>
                                <span v-else-if="item.status=='dispute'" class="badge badge-pill badge-secondary mr-10" style="text-transform: capitalize;">{{ item.status.charAt(0) }}</span>
                                <span v-else-if="item.status=='withdraw'" class="badge badge-pill badge-primary mr-10" style="text-transform: capitalize;">{{ item.status.charAt(0) }}</span>
                                {{item.gname}}
                            </td>
                            <td @click="auth4ViewDetail(item.tid)">{{item.name_m}}</td>
                            <td>{{(item.bank_name==null||item.bank_name===undefined)?'INC':item.bank_name}}</td>
                            <td>{{item.tsum}}<span v-show="item.cost*item.coupon<10000" class="text-warning text-bold">*</span></td>
                            <td>{{ item.coupon }}</td>
                            <td>{{item.publisher}}</td>
                            <!-- <td>{{item.tid.slice(0,5)+'...'+item.tid.slice(-5)}}</td> -->
                            <td>
                                <span @click="auth4ViewDetail(item.tid)">{{item.tid.slice(-9).toUpperCase()}}</span>
                                <span @click="viewTrak(item.tracking)" v-if="smode=='pay'&&item.tracking!=null" class="parcel"><i class="fas fa-truck"></i></span>
                            </td>
                            <td @click="auth4ViewDetail(item.tid)">{{item.adate}}</td>
                            <td @click="auth4ViewDetail(item.tid)"><i class="fas fa-angle-right"></i></td>
                    </tr>
                </table>
                <p v-else-if="countsOfTras==0">해당하는 결과가 없습니다.</p>
                <p v-else>결과를 불러오지 못했습니다.</p>
            </div><!-- card-body-->
        </div>   

        <form action="" v-on:submit.prevent="submitForm">
            <!-- 컴포넌트 연동-->
            <Modal v-show="isVisible" @close="closeModal">
                <div class="card-title" slot="header">
                    사용자 확인
                </div>
                <template slot="body">
                    <p class="guide-text">{{ guideText }}</p>
                    <div>
                        <input type="password" id="modal_pw" v-model="passwd"/>
                    </div>
                </template>
                <template slot="footer">
                    <button type="submit" class="block primary">확인</button>
                </template>
            </Modal>
        </form>

    </div>

</template>
<script>
import JsonExcel from 'vue-json-excel' // JsonExcel Comp.
import Modal from './Modal.vue'
import api from '@/utils/api'

export default {
    name:'ResultBtras',
    components: {
        JsonExcel, // JsonExcel Comp.
        Modal,
    },
    data() {
        return {
            isVisible:false,
            guideText:'',
            passwd:'',
            curTid:'',
            // logStatus:{
            //     purpose:'',
            //     viewDet:false,
            //     downEx:false,
            // },
            dnExcelName:'order.xls',
            orderFields: {
                '확인내용':1,
                '기준일자':{
                    callback:()=>{
                        const td = new Date()
                        return `${td.getMonth()+1}/${td.getDate()}`
                    }
                },
                '주문일자':{
                    callback:(tra)=>{
                        const od = new Date(tra.adate)
                        return `${od.getFullYear().toString()}${('0'+(od.getMonth()+1)).slice(-2)}${('0'+(od.getDate())).slice(-2)}`
                    }
                },
                'ZIP':'zip',
                '주소': {
                    callback: (tra)=>{
                        return `${tra.fixed} ${tra.detail}` 
                    }
                },
                '수취인명':'jtitle',
                '연락처':'tel',
                '주문번호':'tid',
                'ISBN':'isbn13',
                '품목명':'gname',
                '출판사':'publisher',
                '총판':1,
                '수량':{
                    callback:()=>1
                },
                '정가':'cost',
                '할인율':'coupon',
                '결제액':'tsum',
            },
            sln:true,
        }
    },
    props:['td','days','smode'],
    computed:{
        /* ...mapGetters({
            countsOfTras:'getCounts',
            results:'tras',
            // 검색결과 테이블을 보여줄 지 여부
            showResults:'keepResults'  
        }), */
        countsOfTras() {
            return this.$parent.countsOfTras
        },
        resultData() {
            return this.$parent.results
        },
        authorized4Det() {
            return this.$parent.reAuthorized.auth
        },
        logStatus() {
            return {
                downEx: this.$parent.reAuthorized.downEx,
                viewDet: this.$parent.reAuthorized.viewDet,
                purpose: this.$parent.reAuthorized.purpose,
            }
        },
    },
    methods:{
        submitForm() {
            let reAuth = this.logStatus
            // 패스워드 인증 후 오픈
            api.login({ userid:localStorage.getItem('instapay_id'), password:this.passwd })
            .then((response)=>{
                if(response.data.result === 'ok') {
                    localStorage.setItem('instapay_token', response.data.token);
                    api.updateToken()
                    console.log('instapay_token', response.data.token);
                    if(reAuth.purpose=='viewDet') {
                        this.viewTras(this.curTid)
                        //this.log.viewDet = true
                        reAuth.viewDet = true
                        this.isVisible = false
                    }
                    else {
                        //this.log.downEx = true
                        reAuth.downEx = true
                        this.isVisible = false
                    }
                    api.writeLogPurpose('bookorder', reAuth);

                    // update login status
                    //this.$emit('authorized',true)
                    reAuth.auth = true
                    this.$emit('authorized',reAuth)
                    
                }
            }).catch((error)=>{
                console.log(error.message)
                //this.$emit('authorized',false)
                reAuth.auth = false
                this.$emit('authorized',reAuth)
            })
        },
        auth4ViewDetail(tid) {
            let reStat = this.logStatus
            //this.log.purpose = 'viewDet'
            reStat.purpose = 'viewDet'
            if(this.authorized4Det) {
                if(!reStat.viewDet) {
                    api.writeLogPurpose('bookorder', reStat);
                    reStat.viewDet = true
                }
                this.$emit('authorized',{ auth:true, ...reStat })
                this.viewTras(tid);
                return;
            }
            this.guideText = "로그인 비밀번호를 입력해 주십시오."
            this.curTid = tid
            this.isVisible = true
        },
        auth4ExcelDownload() {
            let reStat = this.logStatus
            //this.log.purpose = 'downExcel'
            reStat.purpose = 'downExcel'
            // update purpose
            this.$emit('authorized',{ auth:this.authorized4Det, ...reStat })

            // downEx status가 false인 상황에서는 무조건 다시 로그인 
            this.guideText = "로그인 비밀번호를 입력하여 로그인 후 다시 다운로드 버튼을 눌러 주십시오."
            this.isVisible = true
        },
        closeModal() {
            this.isVisible = false;
        },
        viewTras(tid) {
            // vuex store에 선택된 아이템(tid)을 저장
            //console.log(tid);
            this.$router.push('/btras/detail');
            // vuex mutation commit을 통해 현재 열릴 tid를 저장 
            this.$store.commit('setCurTid', tid);   
            //sessionStorage.setItem('curTid',tid);
        },
        viewTrak(pnum) {
            window.alert(pnum)
        },
        setDownloadName(){
            this.dnExcelName = 'tras'+(new Date()).toISOString()+'.xls' 
        },
        downloadExcel(){
            if(this.smode=='pay') {
                console.log()
            }
            else {
                //location.href=`https://api.instapay.kr/v3/dnexcelorder?t=${this.td}&w=${this.days}`
                api.downloadExcel(this.td, this.days)
            }
        },
    }
}
</script>

<style scoped>
table { width:100%; border-collapse:collapse; table-layout:fixed; }
th, td { text-align:left; padding:0.25rem 0.5rem; overflow:hidden; white-space:nowrap;text-overflow:ellipsis; }
tr.hoverable:hover { /*cursor:pointer;*/ background-color:var(--bg-lightgrey); }
tr.hoverable > td:nth-child(2):hover { cursor:pointer;color:var(--primary) }
tr.hoverable > td:nth-child(7) > span:hover { cursor:pointer;color:var(--primary) }
tr.hoverable > td:nth-child(8):hover { cursor:pointer;color:var(--primary) }
tr.hoverable > td:nth-child(9):hover { cursor:pointer;color:var(--primary) }

tr.hoverable > td:nth-child(7) > .parcel { padding:0.25rem 0.5rem; font-size:0.75rem;}

.btn-excel.authorized { color:var(--primary) }
button.btn-excel { color:var(--text-dark) }

.card.result table>tr>th:first-child, .card.result table>tr>td:first-child { width:30%;}
.card.result table>tr>th:nth-child(3) { width:80px;}
.card.result table>tr>th:nth-child(4) { width:90px;}
.card.result table>tr>th:nth-child(5) { width:60px;}
.card.result table>tr>th:nth-last-child(2) { width:150px;}
.card.result table>tr>th:last-child { width:26px;}

#modal_pw { 
		display: block;
		width: 100%;
		padding: 6px 12px;
		font-size: 14px;
		height: 36px;
		margin-bottom: 0.5rem;}

</style>